// 个人中心首页
<template>
    <div class="personal">
        <!-- <top-bar></top-bar> -->
        <table-bar @toggleTab='toggleTab'></table-bar>
        <div class="content">
            <div class="list">
                <base-list :data='listData' v-if="listData.length > 0" @nextPage='nextPage'></base-list>
                <div class="empty-page" v-else>
                    <img src="../../assets/list-empty.png" class="empty-pic">
                    <span class="empty-text">{{$t('empty.myListEmptytop')}}</span>
                    <span class="empty-text">{{$t('empty.myListEmptyBottom')}}</span>
                </div>
            </div>
        </div>
        <Go-home></Go-home>
        <img src="../../assets/subscibe.png" class="subscibe-pic" @click="editSubscibe" v-if="lang == 'zh'">
        <img src="../../assets/subscibe-en.png" class="subscibe-pic" @click="editSubscibe" v-else>
    </div>
</template>

<script>
import BaseList from '../../components/common/list';
import GoHome from '../../components/common/go-home';
import TableBar from '../../components/personal/table-bar';
import TopBar from '../../components/personal/top-bar';
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
import config from '../../config.js';
import { mapState } from 'vuex';
export default {
    data () {
        return {
            navId: '', // 导航id
            listData: [], // 列表数据
            postData: {
                pageNo: 1,
                pageSize: 10,
                userList: config.userList
            },
            type: 'mySublist', // 类型
            lang: config.lang
        };
    },
    computed: {
        ...mapState({
            personalData: state => state.personal.personalData
        })
    },
    methods: {
        // 切换类型数据
        toggleTab (type) {
            // 请求数据
            this.postData = {
                pageNo: 1,
                pageSize: 10,
                userList: config.userList
            };
            // 存放tab类型
            this.type = type;
            this.getListData(type);
        },
        // 编辑
        editSubscibe () {
            if (this.personalData && this.personalData.userId) {
                this.$router.push({
                    path: '/edit-subscibe',
                    query: {
                        userId: this.personalData.userId ? this.personalData.userId : ''
                    }
                });
            } else {
                this.$router.push({
                    path: '/edit-subscibe'
                });
            }
        },
        // 获取列表数据
        getListData (tpye = 'mySublist') {
            publicPost(getApiUrl(tpye), this.postData).then((res) => {
                if (this.postData.pageNo === 1) {
                    this.listData = res;
                } else {
                    this.listData = this.listData.concat(res);
                }
            }).catch(() => {
                console.warn('error');
            });
        },
        // 获取下一页数据
        nextPage (page) {
            this.postData.pageNo = page;
            this.getListData(this.type);
        }
    },
    components: {
        BaseList,
        GoHome,
        TableBar,
        TopBar
    },
    created () {
        this.$router.setTitle(this.$t('title.personal'));
        this.getListData();
    }
};
</script>

<style scoped lang='scss'>
    @import "../../scss/variables.scss";
    .personal {
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        .subscibe-pic {
            width: pxtorem(64px);
            height: pxtorem(40px);
            position: fixed;
            left:0;
            bottom: pxtorem(28px);
        }
    }
    .content {
        -webkit-overflow-scrolling: touch;
        flex:1;
        overflow: auto;
        background: #fff;
    }
</style>
