<template>
    <ul class="table">
        <li
            v-for="(item, index) in tabList"
            :key="index"
            :class='ind == index  ? "active" : "" '
            @click="toggleTab(item,index)">
            {{$t('personal.'+ item.name)}}
        </li>
    </ul>
</template>

<script>

export default {
    props: ['page'],
    data () {
        return {
            tabList: [
                {
                    name: 'subscribe',
                    type: 'mySublist'
                },
                {
                    name: 'publish',
                    type: 'getMyList'
                },
                {
                    name: 'favorite',
                    type: 'myFavoritelist'
                }
            ],
            ind: 0
        };
    },
    methods: {
        toggleTab (item, index) {
            this.$emit('toggleTab', item.type);
            this.ind = index;
        }
    }
};
</script>

